import axios from 'axios'
// axios.defaults.baseURL = 'http://localhost:8080/ChatRobot'
axios.defaults.baseURL = 'http://localhost:8089/Address/'
export const requestLogin = params => {
  return axios.post('/user/login.do', params).then(
    res => res.data
  )
}
export const registerUser = params => {
  console.log('registerUserAPI：')
  console.log(params)
  return axios.post('/user/register.do', params).then(
    res => res.data
  )
}

export const listStoreInfo = params => {
  console.log('listInfoAPI：')
  console.log(params)
  return axios.post('/store/showStore.do', params).then(
    res => res.data
  )
}

export const getFoodListPage = params => {
  return axios.post('/food/showfoods.do', { params: params }).then(
    res => res.data
  )
}

export const editFood = params => {
  return axios.post('/food/updateFood.do', { params: params }).then(
    res => res.data
  )
}

export const addFood = params => {
  return axios.post('/food/addFood.do', { params: params }).then(
    res => res.data
  )
}

export const removeFood = params => {
  return axios.post('/food/removeFood.do', { params: params }).then(
    res => res.data
  )
}

export const batchRemoveFood = params => {
  return axios.post('/food/removeFood.do', { params: params }).then(
    res => res.data
  )
}

// noinspection JSAnnotator
export const getAddress = params => {
  return axios.post('rest/nonAddress', params).then(
    res => res.data
  )
}



