<template>
  <section>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0;">
      <h1>非标地址转换接口系统</h1>
    <el-form :inline="true">
      <el-form-item>
        <el-select v-model="values" placeholder="请选择显示个数">
        <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
        </el-option>
        </el-select>
      </el-form-item>
    <el-form-item>
    <el-input v-model="nonAddress" placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
    <el-button @click="getAddress()">查询</el-button>
    </el-form-item>
      <el-table :data="matchAddress" highlight-current-row style="width: 100%;">
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="qdz" label="地名" width="300" sortable>
      </el-table-column>
      <el-table-column prop="zxjd" label="经度" width="200" sortable>
      </el-table-column>
      <el-table-column prop="zxwd" label="纬度" width="200" sortable>
      </el-table-column>
      </el-table>
    </el-form>
    </el-col>
  </section>
</template>
<script>
import { getAddress } from '../../api/api'
export default {
  data () {
    return {
      nonAddress: '盛华园5栋503',
      options: [{
        label: '1',
        value: 1
      }, {
        label: '2',
        value: 2
      }, {
        label: '3',
        value: 3
      }, {
        label: '4',
        value: 4
      }, {
        label: '5',
        value: 5
      },
      {
        label: '6',
        value: 6
      }, {
        label: '7',
        value: 7
      }, {
        label: '8',
        value: 8
      }, {
        label: '9',
        value: 9
      }],
      values: 9,
      matchAddress: []
    }
  },
  methods: {
    getAddress () {
      console.log(this.nonAddress)
      let para = {
        nonAddress: this.nonAddress
      }
      getAddress(para).then((result) => {
        console.log(result.data)
        console.log(this.values)
        if (result.data.length === 0) {
          // 弹窗提示
        }
        this.matchAddress = result.data
        if (this.matchAddress.length > this.values) {
          this.matchAddress = this.matchAddress.slice(0, this.values)
        }
      })
    }
  }
}

</script>



